.event-section {
  margin: 24px;
  display: flex;
  border-radius: 6px;
  padding: 12px 6px 12px 6px;
}

.event-record {
  cursor: pointer;
  border: 2px solid #ccc;
  border-radius: 12px;
}

.event-section aside {
  min-width: 192px;
  display: flex;
  justify-content: center;
}

.event-section img {
  max-width: 192px;
  max-height: 160px;
  align-self: center;
}

.event-header {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.army-lists {
  display: flex;
  justify-content: space-between;
  width: 66vw;
  margin-bottom: 24px;
  border: 2px solid #ccc;
  border-radius: 12px;
}

.army-list {
  min-height: 100%;
  padding: 16px;
  width: 100%;
  text-align: center;
}

.army-list.attacker {
  border-right: 1px solid #ccc;
}

.army-list.defender {
  border-left: 1px solid #ccc;
}

.event-summary {
  width: 80vw;
}

.event-summary > aside {
  padding: 8px 0;
}

.event-summary > aside > p {
  padding: 8px;
}

img.event-main {
  max-height: 600px;
  padding-bottom: 24px;
}

img.event-faction {
  max-height: 200px;
  padding-bottom: 16px;
  margin-left: auto;
  margin-right: auto;
}

.event-quote-card {
  max-width: 30vw;
  border: 4px;
  border-radius: 8px;
  padding: 8px;
  margin: auto;
  background-color: rgba(25, 25, 25, 0.4);
  color: #fff;
  margin-bottom: 2em;
  clear: both;
}

.event-quote {
  border-left: 4px solid #fff;
  font-style: italic;
  margin: 8px;
}

.event-quote p {
  font-style: italic;
  padding: 4px;
}

div.thumb {
  width: auto;
  background-color: transparent;
  margin-bottom: 0.5em;
}

div.tleft {
  margin: 0.5em 1.4em 1.3em 0;
  clear: left;
  float: left;
}

div.tright {
  margin: 0.5em 0 1.3em 1.4em;
  clear: right;
  float: right;
}

div.thumbinner {
  border: 1px solid #ccc;
  background-color: #fff;
  font-size: 94%;
  padding: 3px;
  text-align: center;
  overflow: hidden;
  min-width: 100px;
}

.thumbimage {
  border: 1px solid #ccc;
}

.thumbcaption {
  border: 0;
  font-size: 94%;
  line-height: 1.4em;
  padding: 3px;
}