.font-face-ca {
  font-family: 'CasablancaAntique';
}

#maps-background {
  background-color: #111;
}

#maps-background ul {
  margin: 0;
}

#banner-image {
  background-attachment: fixed;
  background-repeat: no-repeat;
  background-size: cover;
  width: 100%;
  height: 0;
  padding-top: 20%;
}

.home {
  background-image: url('../assets/banners/bfg.png');
}

.background {
  background-image: url('../assets/banners/battlefleet.png');
}

.events {
  background-image: url('../assets/banners/artillery.png');
  background-position: 0 -200px;
}

.factions {
  background-image: url('../assets/banners/tank.png');
}

.characters {
  background-image: url('../assets/banners/big-mek.png');
}

.menu-nav {
  width: 100%;
  background-color: #c6d4e1;
  text-transform: uppercase;
  font-size: .8em;
  display: flex;
  justify-content: space-between;
  position: sticky;
  top: 0;
  z-index: 2;
}

.navigation {
  display: flex;
  margin: 0;
  padding: 12px 6px 12px 6px;
  user-select: none;
}

.navigation li {
  padding: 0;
  color: #545454;
}

.navigation li:not(:first-child):before {
  content: '|';
  color: #a4bacf;
  margin-right: 20px;
}

.navigation li a {
  margin-right: 20px;
  padding: 7.5px 10px;
  text-decoration: none;
  text-transform: inherit;
}

.menu-active {
  background: rgba(25, 25, 25, 0.2);
  color: #fff;
}

.home-img {
  align-self: center;
  height: 16px;
  margin-right: 10px;
  filter: invert(30%) sepia(11%) saturate(4%) hue-rotate(340deg) brightness(94%) contrast(79%);
}

article {
  padding: 20px;
}

article p {
  padding-bottom: 16px;
}

article ul {
  margin: 0;
}

.timeline li {
  padding: 8px;
}

.symbol {
  height: 96px;
  margin: auto;
  opacity: 0.75;
  padding-top: 32px;
}

.sub-nav {
  margin: auto;
}

.sub-nav ul li {
  color: #aaa;
  margin-right: 20px;
  font-family: 'CasablancaAntique';
  cursor: pointer;
}