/* Styling for overlay layers and images */

#underlay {
  display: inline-block;
  position: relative;
}

#underlay > img {
  height: auto;
  width: auto;
  max-width: 100%;
}

.location-markup {
  display: none;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}

.location-markup.active {
  display: block;
}

.closeup {
  display: none;
  position: absolute;
  top: 0;
  height: auto;
  width: auto;
  max-width: 100%;
  opacity: 1;
  background-image: url('../../assets/maps/overlay.png');
  background-position: 50%;
  background-repeat: no-repeat;
  background-size: cover;
}

.closeup.active {
  display: inline-block;
}

.closeup-markup {
  display: none;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}

.closeup-markup.active {
  display: block;
}

.system-details {
  display: none;
  position: absolute;
  top: 5%;
  right: 5%;
  padding: 12px;
  color: #eee;
  background-color: #111;
  border: 2px solid #ccc;
  box-shadow: 3px 3px 3px #666;
  border-radius: 8px;
  width: 30%;
  text-align: center;
}

.system-details.active {
  display: block;
}

.system-map {
  position: relative;
}

.planet-orbit {
  position: absolute;
  display: flex;
}

.planet-orbit figcaption {
  -webkit-transform: translateX(-50%) translateY(-160%);
  -moz-transform: translateX(-50%) translateY(-160%);
  -ms-transform: translateX(-50%) translateY(-160%);
  -o-transform: translateX(-50%) translateY(-160%);
  transform: translateX(-50%) translateY(-160%);
  position: absolute;
  margin: 0;
  padding: 0 4px;
  font-size: .7rem;
  font-weight: lighter;
  white-space: nowrap;
  color: white;
  background-color: black;
  border-radius: 6px;
  display: none;
}

.planet-orbit:hover figcaption {
  display: block;
}

.planet-orbit-img {
  height: 24px;
  width: 24px;
  -webkit-transform: translateX(-50%) translateY(-50%);
  -moz-transform: translateX(-50%) translateY(-50%);
  -ms-transform: translateX(-50%) translateY(-50%);
  -o-transform: translateX(-50%) translateY(-50%);
  transform: translateX(-50%) translateY(-50%);
}

/* General styling for markers */

.marker {
  position: absolute;
  height: 24px;
  width: 24px;
  filter: invert(100%) sepia(0%) saturate(7459%) hue-rotate(110deg) brightness(103%) contrast(96%) drop-shadow(0px 0px 1px #eee);
}

.marker.hidden {
  display: none;
}

.marker span {
  -webkit-transform: translateX(-50%) translateY(-160%);
  -moz-transform: translateX(-50%) translateY(-160%);
  -ms-transform: translateX(-50%) translateY(-160%);
  -o-transform: translateX(-50%) translateY(-160%);
  transform: translateX(-50%) translateY(-160%);
  position: absolute;
  margin: 0;
  padding: 0 4px;
  font-size: .7rem;
  font-weight: lighter;
  white-space: nowrap;
  color: black;
  background-color: white;
  border-radius: 6px;
}

.marker-content {
  -webkit-transform: translateX(-50%) translateY(-50%);
  -moz-transform: translateX(-50%) translateY(-50%);
  -ms-transform: translateX(-50%) translateY(-50%);
  -o-transform: translateX(-50%) translateY(-50%);
  transform: translateX(-50%) translateY(-50%);
}

.closeup-markup .marker {
  filter: invert(0%) sepia(6%) saturate(24%) hue-rotate(268deg) brightness(107%) contrast(107%);
}

.closeup-markup .marker span {
  display: none;
}

.closeup-markup .marker .marker-content {
  cursor: pointer;
}

.closeup-markup .marker:hover span {
  display: block;
}

