/* Index sections */

.planet-index {
  position: relative;
}

.system-section {
  width: 100%;
  text-align: center;
  background-color: #111;
  color: #ccc;
  padding: 64px 0;
}

.system-section ul {
  width: auto;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.planet-section {
  margin: 16px 24px;
  padding: 6px 12px;
  width: 224px;
  border: 2px solid #aaa;
  border-radius: 12px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.planet-section img {
  width: 128px;
  height: 128px;
}