.faction-section {
  margin: 24px;
  display: flex;
  border-radius: 6px;
  padding: 12px 6px 12px 6px;
}

.faction-section aside {
  min-width: 192px;
  display: flex;
  justify-content: center;
}

.faction-section img {
  max-width: 192px;
  max-height: 160px;
  align-self: center;
}

.faction-section article {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.faction-section p {
  height: 100%;
}