.char-list {
  display: grid;
  grid-template-columns: 1fr 1fr;
}

.char-li {
  margin: 12px;
}

.character-section {
  border: 2px solid #ccc;
  display: flex;
  flex-direction: column;
  border-radius: 12px;
  padding: 12px 6px 12px 6px;
  overflow-wrap: break-word;
  cursor: pointer;
  height: auto;
}

.char-card {
  display: flex;
}

.char-card aside {
  min-width: 192px;
  height: 192px;
  display: flex;
  justify-content: center;
}

.char-card img {
  max-width: 192px;
  max-height: 192px;
  align-self: center;
}

.char-summary {
  width: 100%;
  position: relative;
  overflow: hidden;
  white-space: normal;
  padding: 6px 12px;
}

.char-summary.compress {
  max-height: 192px;
}

.background-emblem {
  position: absolute;
  top: 96px;
  right: 0;
  transform: translateY(-50%);
  opacity: 0.1;
}

.char-stats {
  display: none;
  padding: 0 12px;
}

.char-stats.expand {
  display: block;
}

.char-desc {
  display: none;
  padding: 12px 0 6px;
}

.char-desc.expand {
  display: inline-block;
}

.campaign-info {
  padding: 12px 0;
}

.hidden {
  display: none;
}

table {
  border-collapse: collapse;
  border: none;
  margin-bottom: 36px;
}

table:last-child {
  margin-bottom: 0;
}

table tr {
  border-bottom: 1px solid #111;
}

table th, td {
  border-right: 1px solid #111;
  padding: 2px 6px;
  text-align: center;
}

table tr:last-child, th:last-child, td:last-child {
  border: none;
}

.weapons-table td {
  font-size: 0.8rem;
  text-align: left;
}
